:root {
  --body-font-family: 'Space Mono', monospace;
  --white-color: #fff;
  --error-color: #d32f2f;
  --warning-color: #f8d7da;
  --body-color: #c5e4e7;
  --primary-color: #00474b;
  --primary-gray-color: #5e7a7d;
  --secondary-color: #26c0ab;
  --form-color: #f3f8fb;
  --box-shadow: 0px 0px 30px #00474b17;
  --border-radius: 1.5rem;
  --form-border-radius: 0.8rem;
  --fw-bold: 700;
  --font-weight-light: 400;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  font-family: inherit;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
  font-size: 1.6rem;
  font-family: 'Space Mono', monospace;
  font-family: var(--body-font-family);
  background-color: #c5e4e7;
  background-color: var(--body-color);
  position: relative;
}

.container {
  position: absolute;
  top: 50%;
  left: 50%;
